import Vue from 'vue'
import App from './App.vue'
import router from '@/router'
import Vue2Editor from "vue2-editor";
// import xpay from './galaxy-ui/lib'
// import xsiderbar from './galaxy-ui/lib'
// import galaxyUi from 'galaxyzz-ui'
import galaxyUi from './galaxyZz-ui'
import Code from '@/view/code'
import './assets/publicCss.css'
import scrollTop from './directives/ScrollTop'
// Vue.config.productionTip = false;
// Vue.use(xpay);
// Vue.use(xsiderbar)
Vue.use(galaxyUi);
Vue.use(Vue2Editor);
Vue.component('Code', Code);
Vue.directive('scrollTop', scrollTop)
Vue.directive('drag', {
  // 当指令绑定到元素上的时候执行 
  bind(el, binding) {
    console.log('bind');
    // 当前指令绑定的dom元素 
    //console.log(el); 
    // 指令传入的参数、修饰符、值 v-指令名称:参数.修饰符=值 
    // console.log(binding)
    el.onmousedown = function (e) {
      var e = e || event;
      let disX = e.clientX - el.offsetLeft;
      let disY = e.clientY - el.offsetTop;

      document.onmousemove = function (e) {
        var e = e || event;
        let L = e.clientX - disX;
        let T = e.clientY - disY;
        if (binding.modifiers.limit) {
          if (L < 0) {
            L = 0;
          }
        }
        el.style.left = L + 'px';
        el.style.top = T + 'px';
      };
      document.onmouseup = function () {
        document.onmousemove = null;
      };
      return false;
    }
  }
});
new Vue({
  router,
  render: h => h(App),
}).$mount('#app')
